
.mainFilter{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    position: relative;
    top: 6rem;
    color: white;
}
a{
    text-decoration: none;
    color: rgb(97,97,97);
}
li{
    list-style: none;
}
.btns{
    padding: 10px 20px;
    margin-right: 6px;
    background: transparent;
    border: none;
    border: 0.6px solid #ccc;
    border-radius: 5px;
    color: #323232;
    cursor: pointer;
}